<template>
  <a-layout class="container">
    <!--  -->
    <!--  -->
    <div class="title">
      <a-button @click="allClear" danger></a-button>
      <a-button @click="clear" danger></a-button>
      <a-button class="title-left" @click="undo"></a-button>
      <a-button class="title-right" @click="jsonShow">JSON</a-button>
      <a-button @click="jsonInput">JSON</a-button>
      <a-button></a-button>
      <a-button type="primary" @click="publishConfirm"></a-button>
    </div>
    
    <a-divider class="divider"></a-divider>
  
    <div style="display: flex; justify-content: center;">
      <a-input 
        v-model:value="formTitle"
        :bordered="false"
        class="form-title"
      ></a-input>
    </div>
    <a-divider></a-divider>
    
    <!-- 
       height: 100% 
       :sort="false" 

      error happens when template have commented elements:
      Item slot must have only one child

       component
       v-if v-else-if 
    -->
    <draggable
      v-model="componentList"
      item-key="id"
      :group="{ name: 'component' }"
      :sort="false"
      style="height: 100%"
      animation="200"
    >
      <template #item="{ element }">
        <div
          :class="selected.id == element.id ? 'item-content active' : 'item-content'"
          @click="() => select(element)"
        >
          <FormGenerator :element="element" />
        </div>
      </template>
    </draggable>
    
    <!--  -->
    <!--  JSON  -->
    <a-modal 
      v-model:visible="jsonShowVisible" 
      title=" JSON" 
      cancelText=" "
      okText=""
      :destroyOnClose=true
      @ok="jsonShowOk"
    >
      <a-typography-paragraph :copyable="{ text: formDataJson }">
        {{ formDataJson }}
      </a-typography-paragraph>
    </a-modal>
    <!--  JSON  -->
    <a-modal 
      v-model:visible="jsonInputVisible" 
      title=" JSON" 
      okText=""
      cancelText=""
      :destroyOnClose=true
      @ok="jsonInputOk"
      @cancel="jsonInputCancel"
    >
      <p> JSON </p>
      <a-textarea 
        v-model:value="jsonInputValue"  
        :autoSize="{minRows: 6}"
      />
    </a-modal>
    <!--  -->
    <a-modal 
      v-model:visible="publishVisible" 
      title="" 
      okText=""
      cancelText=""
      :destroyOnClose=true
      @ok="publishConfirmOk"
    >
    </a-modal>
  
  </a-layout>
</template>

<script setup>
import { ref } from "vue";
import draggable from "vuedraggable";
import { message } from 'ant-design-vue';
import FormGenerator from "@/components/Form/FormGenerator.vue";
import { useSelectedComponentStore } from "@/stores/component";
import axios from "axios";

// 
const formTitle = ref("");

//  -> 
const componentList = ref([]);

//  -> 
const stack = [];

// 
const selected = ref({});
// 
const selectedComponent = useSelectedComponentStore();

//  JSON 
const formDataJson = ref("");

//  JSON 
const jsonShowVisible = ref(false);
//  JSON 
const jsonInputVisible = ref(false);
//  JSON 
const jsonInputValue = ref("");

// 
const publishVisible = ref(false);

// 
function select(element) {
  selected.value = element;
  selectedComponent.component = selected.value;
}

// 
function allReset() {
  selected.value = {};
  selectedComponent.component = {
    name: "",
    type: "",
    field: "",
    options: {
      cType: ""
    }
  };
}

// 
function allClear() {
  if(componentList.value.length > 0) {
    stack.push([...componentList.value]);
    componentList.value.length = 0;
    // 
    allReset();
  }
}

// 
function clear() {
  if(selected.value?.id) {
    stack.push(selected.value);
    for(let i = 0; i < componentList.value.length; i++) {
      if(selected.value.id == componentList.value[i].id) {
        componentList.value.splice(i, 1);
        break;
      }
    }
    // 
    allReset();
  }
}

// 
function undo() {
  if(stack.length > 0) {
    componentList.value = componentList.value.concat(stack.pop());
  }
}

//  JSON 
function formJsonCreate() {
  const formData = {
    title: formTitle.value,
    content: componentList.value,
  }
  formDataJson.value = JSON.stringify(formData);
}

//  JSON
function jsonShow() {
  jsonShowVisible.value = true;
  formJsonCreate();
}
//  JSON 
function jsonShowOk(e) {
  jsonShowVisible.value = false;
}

//  JSON
function jsonInput() {
  jsonInputVisible.value = true;
}
//  JSON 
function jsonInputOk(e) {
  if(jsonInputValue.value.length > 0) {
    // 
    allReset();
    const formData = JSON.parse(jsonInputValue.value);
    formTitle.value = formData.title;
    componentList.value = formData.content;
    message.success('JSON ', 2);
  }
  jsonInputValue.value = "";
  jsonInputVisible.value = false;
}
//  JSON 
function jsonInputCancel(e) {
  jsonInputValue.value = "";
  jsonInputVisible.value = false;
}

// 
function publishConfirm() {
  publishVisible.value = true;
}
// 
function publishConfirmOk(e) {
  publishVisible.value = false;
  publish();
}

// // 
// function publish() {
//   formJsonCreate();
//   axios.post('http://localhost:3000/posts', {
//     formData: formDataJson.value
//   })
//   .then((result) => {
//     console.log(result.data.formData);
//   }).catch((err) => {
//     console.log(err);
//   });
// }
function publish() {
  // formJsonCreate();
  // console.log(formDataJson.value)
  // 
  var params = new URLSearchParams()
  // 
  formJsonCreate();
  params.append("formData",formDataJson.value)
  // params.append("url","123")
  axios.post('formUrl.do',params)
  .then((result) => {
    console.log(result.data);
    var mess = result.data.substring(0, 14);
    var randUrl = result.data.substring(14);
    if(mess=="Form released!")
      window.location.href = "http://localhost:8081/demo_war_exploded/index.html#/admin/formFinish?randUrl="+randUrl;
  }).catch((err) => {
    console.log(err);
  });
}


</script>

<style scoped>
.container {
  height: 100%;
  /*  */
  overflow-y: scroll;
}

.title {
  height: 40px;
  color: black;
  font-size: 1rem;
  font-family:  Georgia, 'Times New Roman', Times, serif;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  /*  */
  margin: 4px 10px;
} 

/*  */
.title-left {
  margin-right: auto;
}

/*  */
.title-right {
  margin-left: auto;
}

.form-title {
  text-align: center;
  font-size: 1rem;
  font-family:  Georgia, 'Times New Roman', Times, serif;
  width: 50%;
  border: 1px dashed rgba(55, 55, 55, 0.8);
}

/* 
  border-color 
  background-color 
*/
.divider {
  margin-top: 0;
  border-color: #1890ff;
}

.item-content {
  margin: 10px;
  border: 1px dashed rgba(55, 55, 55, 0.8);
}

.item-content.active {
  border: rgb(120, 200, 255) 1px solid;
}
</style>